import Vue from 'vue'
import Router from 'vue-router'
import my from '@/pages/my/index'
import index from '@/pages/index/index'
import vodes from '@/pages/vodes/index'
import rodes from '@/pages/rodes/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/my',
      name: 'my',
      component: my
    },
    {
      path: '/vodes',
      name: 'vodes',
      component: vodes
    },
    {
      path: '/rodes',
      name: 'rodes',
      component: rodes
    }
  ]
})



