<template>
  <div class="hello">
       <!-- 搜索 -->
       <div class="navtio">
             <div class="lefr">
               <van-icon name="arrow-left" color="#fff" size="20" />
             </div>
             <div class="input">
                 <div class="ioce">
                      <van-icon name="search" color="#000" size="20"/>
                 </div>
                 <input type="text" placeholder="输入商品/用户/动态等关键词">
             </div>
             <div class="scuer">搜索</div>
       </div>
       <!-- 历史搜索 -->
       <div class="lsie">
            <div class="nake">
                  <div>历史搜索</div>
                  <div>清空</div>
            </div>
            <div class="clone">
                   <div class="nrs" v-for="(itme,index) in listdata " :key="index" @click="chang(index)">
                        <div style="font-size: 12px;">{{ itme.name }}</div>
                        <div class="cloioce">
                          <van-icon name="cross" color="#fff" size="10"/>
                        </div>
                   </div>
            </div>
       </div>
       <!-- 热门搜索 -->
       <div class="lsie">
            <div class="nake">
                  <div>热门搜索</div>
            </div>
            <div class="rengm">
                   <div class="nrs" v-for="(itme,index) in list" :key="index">
                        <div style="font-size: 12px;color: #fff;" class="choexs" :style="index == 0 || index == 1? 'background-color: red'  :  index == 2 ? 'background-color: #d28a23' : ''">{{ index + 1 }}</div>
                        <div class="cloioce" >
                            {{itme.name  }}
                        </div>
                   </div>
            </div>
       </div>
       <!-- 猜你喜欢 -->
      <div class="lsie">
            <div class="nake">
                  <div>猜你喜欢</div>
            </div>
            <div class="loves">
                   <div class="nrs" v-for="(itme,index) in list" :key="index">
                          <div class="imags">
                               <img src="https://img2.baidu.com/it/u=867579726,2670217964&fm=253&fmt=auto&app=120&f=JPEG?w=1280&h=800" alt="">
                          </div>
                          <div class="tiles">新鲜火龙果阿德撒旦</div>
                   </div>
            </div>
       </div>
      <div style="font-size: 14px">我是</div>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      listdata:[
          {
             name:'车厘子'
          },
          {
            name:'牛奶'

          },
          {
            name:'西瓜'

          },
          {
            name:'香蕉'

          }
      ],
      list:[
      {
             name:'火龙果'
          },
          {
            name:'草莓'

          },
          {
             name:'车厘子'
          },
          {
            name:'牛奶'

          },
          {
             name:'车厘子'
          },
          {
            name:'牛奶'

          },
          {
             name:'车厘子'
          },
          {
            name:'牛奶'

          }
      ]
    }
  },
  methods:{
    chang(index){
         console.log(index)
        if(index == 0){
          this.listdata.splice(0 ,1)
          // console.log('这里')
        }else{
          this.listdata.splice(index ,1)
        }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
    body{
       background-color: #0b0708;
    }
   .hello{
      font-size: 12px;
   }
   .navtio{
       display: flex;
       align-items: center;
   }
   .navtio{
      width: 92%;
      margin: 20px auto; 
   }
   .navtio .lefr{
       width: 10%;
   }
   .navtio .input{
       flex: 1; 
       position: relative;
    } 
    .navtio .input input{
        padding-left: 40px;
        box-sizing: border-box;
        width: 90%;
        margin: 0 auto;
        height:28px;
        border-radius: 20px;
        border: 0;
    }
    .navtio .ioce{
        position: absolute;
        left: 8%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
  .hello .scuer{
        width: 10%;
        color: #956f5b;
    }
    .lsie{
        /* display: flex; */
        width: 87%;
        /* align-items: center; */
        margin: 0 auto 20px;
    }
    .cloioce{
        color: #87888a;
    }
    .nake{
        display: flex;
        color: #956f5b;
        width: 100%;
        align-items: center;
        margin: 0 auto;
        justify-content: space-between;
    }
    .nake div:nth-child(1){
       font-size: 14px;
       position: relative;
       color: #fff;
    }
    .nake div:nth-child(1)::after{
        content: "";
        width: 4px;
        position: absolute;
        height: 15px;
        top: 50%;
        transform: translate(0,-50%);
        left: -25%;                                                                      
        border-radius: 10px;
        background-color: #956f5b;
    }
    .clone{
        display: flex;
    }
    .clone .nrs{
         width: 60px;
         height: 25px;
         margin: 10px 10px 10px 0;
         background-color: #1c1c1e;
         border-radius: 20px;
         display: flex;
         justify-content: space-around;
         padding: 0px 5px;
         align-items: center;
         color: #87888a;
         
    }
    .rengm{
       display: flex;
       flex-wrap: wrap;
    }
    .rengm .nrs{
        width: 50%;
        display: flex;
        margin: 10px 0px;
        align-items: center;
    }
    .choexs{
        width: 20px;
        height: 20px;
        background-color: #1c1c1e;
        margin-right: 10px;
        /* margin-top: 5px; */
        text-align: center;
        line-height: 20px;
        border-radius:2px;
        /* background-color: #fff; */
    }
    .loves{
       display: flex;
       flex-wrap: wrap;
    }
    .loves  .nrs{
      margin: 10px 0px;
      width: 50%;
    }
    .loves .imags{
         width: 90%;
         height:90px;
    }
    .loves .imags img{
        width: 100%;
        border-radius: 5px;
        height: 100%;  
    } 
    .loves .tiles{
        color: #fff;
        margin-top: 5px;
    }
</style>



